import {
    WS_CONNECTION_SUCCESS,
    WS_CONNECTION_ERROR,
    WS_CONNECTION_CLOSED,
    WS_GET_MESSAGE
} from "../actions/wsActionTypes";

const initialState = {
    wsConnected: false,
    orders: [],
    total: null,
    totalToday: null,
    error: undefined
};

export const wsReducer = (state = initialState, action) => {
    switch (action.type) {
        //      WS_CONNECTION_SUCCESS
        //   wsConnected   true
        case WS_CONNECTION_SUCCESS:
            return {
                ...state,
                error: undefined,
                wsConnected: true,
                orders: []
            };

        //      WS_CONNECTION_ERROR
        //   wsConnected   false     action.payload
        case WS_CONNECTION_ERROR:
            return {
                ...state,
                error: action.payload,
                wsConnected: false
            };

        //      WS_CONNECTION_CLOSED,   
        //   wsConnected   false
        case WS_CONNECTION_CLOSED:
            return {
                ...state,
                error: undefined,
                wsConnected: false,
                orders: []
            };

        //      WS_GET_MESSAGE
        //  ,     
        //  messages  ,    
        case WS_GET_MESSAGE:
            return {
                ...state,
                error: undefined,
                orders: [...action.payload.orders],
                total: action.payload.total,
                totalToday: action.payload.totalToday,
            };
        default:
            return state;
    }
};